@import url(https://fonts.googleapis.com/css2?family=Orbitron&family=Roboto&display=swap);
.btn{cursor:pointer;border:none;outline:none;background:#414042;color:#c4c7cc;font-size:24px;transition:color 250ms}.btn:hover,.btn:focus{color:#74ccf4}.btn-arrows{margin:auto 0.5rem}#block-container{width:85%;max-width:600px;display:grid;grid-template-columns:repeat(2, 23%) 1fr repeat(2, 23%);grid-template-areas:"header header header header header" "break-setup break-setup . session-setup session-setup" "timer timer timer timer timer"}#title,#timer-container{margin:0 auto}#title{grid-area:header;margin-bottom:2rem;color:#74ccf4}#break-setup-container{grid-area:break-setup}#session-setup-container{grid-area:session-setup}#timer-container{grid-area:timer}#break-label,#session-label{text-align:center;margin-bottom:0.25rem}#break-length-block,#session-length-block{display:flex;justify-content:center;font-size:24px;margin-bottom:1.5rem}#running-time-section{width:200px;text-align:center;padding:1.5rem 3rem;border:5px solid #74ccf4;border-radius:50px}#time-left{font-size:48px}#start-stop-buttons-container{text-align:center;margin:2rem}@media (max-width: 750px){#main-window{font-size:12px}#block-container{width:95%;grid-template-columns:repeat(2, 22%) 1fr repeat(2, 25%)}#running-time-section{width:150px}#break-length-block,#session-length-block,.btn{font-size:18px}#time-left{font-size:36px}}@media (orientation: landscape) and (max-width: 750px){#title,#start-stop-buttons-container,#break-length-block,#session-length-block{margin-bottom:1rem}#block-container{max-height:100vh}#running-time-section{padding:0.5rem 2rem}#break-length-block,#session-length-block,.btn{font-size:15px}#time-left{font-size:24px}}*{margin:0}#main-window{background:#414042;color:#c4c7cc;width:100%;height:100vh;box-sizing:border-box;display:flex;justify-content:center;align-items:center;font-family:"Orbitron","Roboto",sans-serif;font-size:16px;letter-spacing:1px}

